import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Category from './Category'
import CategoryType from './CategoryType'

const entity = {
  code: {
    displayName: 'code',
  },
  name: {
    displayName: 'name',
  },
  categoryId: {
    displayName: 'labelName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Category.getList,
        itemText: '_langsname',
      },
    },
  },
  categoryTypeId: {
    displayName: 'warengruppe',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },
  price: {
    displayName: 'price',
  },
  totalCount: {
    // displayName: 'totalMoneyCount',
    tableConfig: {},
  },
}
const config = {
  load: async function (filter) {
    let filteredCategory = []
    if (localStorage.getItem('fixedHideCategories')) {
      filteredCategory = JSON.parse(localStorage.getItem('fixedHideCategories'))
    }
    console.log('filteredCategory', filteredCategory)

    console.log('filter', filter)
    const data = {
      start: filter[0].dateFilter[0],
      end: filter[0].dateFilter[1],
    }
    return (await hillo.get('BackendData.php', Object.assign({
      op: 'dishStatistic',
      lang: GlobalSettings.getLang(),
    }, data)))
      .content.filter(item => !(filteredCategory.includes(parseInt(item.categoryId))))
  },
}

export default IKDataEntity.ModelFactory(entity, config)
